	module.exports = {"data":{"markdownRemark":{"html":"<h2>HTML5 Semantic Elements</h2>\n<h3>A Quick History</h3>\n<p>HTML was originally created as a markup language to describe documents on the early internet. As the internet grew and was adopted by more people, it’s needs changed. Where the internet was originally inteded for sharing scientific documents, now people wanted to share other things as well. Very quickly, people started wanting to make the web look nicer. Because the web was not initially built to be designed, people had to hack to get things laid out in different ways. Rather than using the <code>&#x3C;table>&#x3C;/table></code> to describe information using a table, people would use them to position other elements on a page. </p>\n<h3>HTML5</h3>\n<p>HTML5 has put a focus back on elements that are semantically accurate. Semantic elements are elements that clearly describe it’s meaning in a human and machine readable way. This is especially important for accessibility. </p>\n<p>New semantic HTML5 elements include <code>&#x3C;header></code>, <code>&#x3C;nav></code>, <code>&#x3C;section></code>, <code>&#x3C;article></code>, <code>&#x3C;aside></code>, and <code>&#x3C;footer></code>, elements among others. These ones act more or less like <code>&#x3C;div></code> elements in that they group other elements together into page sections. However where a <code>&#x3C;div></code>  tag could contain any type of information, it is easy to identify what sort of information would go in a semantic <code>&#x3C;header></code> region.</p>\n<p><strong>Semantic elements laying out a page by w3schools</strong></p>\n<p><img src=\"https://www.w3schools.com/html/img_sem_elements.gif\" alt=\"Semantic elements laying out a page by w3schools\"></p>\n<h3>Learn more about the new HTML5 elements:</h3>\n<ul>\n<li><a href=\"https://www.w3schools.com/html/html5_semantic_elements.asp\">w3schools</a> provides simple and clear descriptions of many of the news elements and how/where they should be used.</li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Element\">MDN</a> also provides a great reference for all HTML elements and goes deeper into each.</li>\n</ul>","fields":{"slug":"/html/html5-semantic-elements/"},"frontmatter":{"title":"HTML5 Semantic Elements"}}},"pathContext":{"slug":"/html/html5-semantic-elements/"}}

